import React, {useState} from "react";
import "./SelectCategory.scss";
import Modal from "../modal-component/ModalComponent";
import {GET_CATEGORIES} from "../../../../api/queries";
import {graphQL} from "../../../../api/axios";
import Button from "../button-component/ButtonComponent";
import {useSelector} from "react-redux";

// TODO: Change this Component to handle multiple categories
const SelectCategory = (props) => {
  const registryInfo = useSelector(state => state.registry.info)
  const [categories, setCategories] = useState([]);
  const [searchedCategories, setSearchedCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(props.currentCategory ? props.currentCategory : {});

  const getCategories = () => {
    let data = {
      query: GET_CATEGORIES,
      variables: {
        registryId: parseInt(registryInfo.id)
      },
    }
    graphQL(data).then(res => {
      if (res.data.data.categories) {
        let newCategories = res.data.data.categories.map(parentCategory => {
          let newParentCategory = {...parentCategory}
          newParentCategory['displayName'] = parentCategory.categoryName;
          newParentCategory.subCategories = parentCategory.subCategories.map(childCategory => (
            {
              ...childCategory,
              displayName: parentCategory.categoryName + " : " + childCategory.categoryName
            }
          ))
          return newParentCategory;
        })
        setCategories(newCategories);
      }
    }).catch(err => {
      console.error(err.message)
    })
  }

  const closeModal = () => {
    setSelectedCategory(props.currentCategory);
    props.closeModal(false);
  }

  const saveSelectedCategory = () => {
    if (selectedCategory && selectedCategory.id) {
      props.changeCurrentCategory(selectedCategory)
    }
    props.closeModal(false);
  }

  if (categories.length === 0) {
    getCategories();
  }

  const getCategoriesToDisplay = (cat) => {
    return (
      <div className="col category-columns">
        {cat.map(category => {
          return (
            <div className="mb-6 category-column" key={"select-" + category.categoryName}>
              <h4 className="parent-category mb-3 font-medium px-2">{category.categoryName}</h4>
              {category.subCategories ?
                category.subCategories.map(subCategory =>
                  <p
                    key={"select-child-" + subCategory.categoryName}
                    className={["px-2 py-1 child-category", (selectedCategory && selectedCategory.id) && (selectedCategory.id === subCategory.id) ? "selected-category" : ""].join(" ")}
                    onClick={() => setSelectedCategory(subCategory)}
                  >{subCategory.categoryName}</p>)
                : null}
            </div>
          )
        })}
      </div>
    )
  }

  const categorySelectBody = (
    <div className="row select-category-body">
      {searchedCategories.length > 0 ? getCategoriesToDisplay(searchedCategories) : getCategoriesToDisplay(categories)}
    </div>
  )

  const saveButton = (
    <Button
      variant="primary"
      className="font-medium p-1"
      onClick={saveSelectedCategory}
    >Done</Button>
  )

  // Search Category Section
  const searchInputRef = React.createRef()
  const changeLinkRef = React.createRef()

  const focusSearchCategoryInput = () => {
    if (searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }

  const searchInputFocusHandler = (e) => {
    if (!e.target.classList.contains("search-category-input-focus")) {
      e.target.classList.add("search-category-input-focus");
    }
  }

  const searchInputBlurHandler = (e) => {
    if (e.target.value.length > 0) {
      return
    }

    if (e.target.classList.contains("search-category-input-focus")) {
      e.target.classList.remove("search-category-input-focus");
    }
  }

  const searchCategoryInputChangeHandler = (e) => {
    if (e.target.value.length > 0) {
      changeLinkRef.current.style.display = "block";

      //  Searching through the category
      let finalCategories = []
      for (const parentCategory of categories) {
        let newParentCategory = {...parentCategory}
        newParentCategory.subCategories = []
        let searchMatched = false

        for (const subCategory of parentCategory.subCategories) {
          if (subCategory.categoryName.toLowerCase().match(e.target.value.toLowerCase())) {
            searchMatched = true
            newParentCategory.subCategories.push(subCategory)
          }
        }

        if (searchMatched) {
          finalCategories.push(newParentCategory)
          searchMatched = false
        }
      }
      setSearchedCategories(finalCategories)
    } else {
      changeLinkRef.current.style.display = "none";
    }
  }

  const changeLinkClickHandler = (e) => {
    e.preventDefault();
    if (searchInputRef.current) {
      if (searchInputRef.current.value.length > 0) {
        searchInputRef.current.value = ""
      }
      changeLinkRef.current.style.display = "none";
      searchInputRef.current.focus()
      setSearchedCategories([])
    }
  }

  const headerContent = (
    <div className="align-self-center d-flex position-relative select-category-search-section mr-3">
      <input
        type="text"
        placeholder="Search category..."
        className="search-category-input pl-5 py-1 pr-1 text-body"
        ref={searchInputRef}
        onChange={(e) => searchCategoryInputChangeHandler(e)}
        onFocus={(e) => searchInputFocusHandler(e)}
        onBlur={(e) => searchInputBlurHandler(e)}
      />
      <i className="icon-search search-category-icon" onClick={focusSearchCategoryInput}/>
      <a
        className="search-category-clear-link text-sm font-medium"
        href="#"
        onClick={(e) => changeLinkClickHandler(e)}
        ref={changeLinkRef}>Clear all</a>
    </div>
  )

  return (
    <Modal
      show={props.showModal}
      title="Select Category"
      footer={saveButton}
      headerContent={headerContent}
      closeModal={closeModal}>
      {categorySelectBody}
    </Modal>
  )
}

export default SelectCategory;